    (
        var(--cw-mega-menu-top-offset, 0px) -
          min(
            var(--cw-mega-menu-top-offset, 0px),
            var(--cw-polestar-app-scroll-y, 0px)
          )
      ) +
      (
        var(--cw-mega-menu-block-size, 0px) *
          (1 - var(--cw-mega-menu-hide-header-progress, 0))
